  -webkit-box-shadow: 0 0 0px 1000px white inset !important;
}

.form-control {
  border: 1px solid #dee2e6;
  box-shadow: none;
  color: #333;
  padding: .54688rem .875rem;
  font-size: .875rem;
  height: calc(1.6em + 1.21875rem);
  line-height: 1.6;
  font-weight: 400;
  color: #1e2022;
}

.form-control:focus {
  box-shadow: 0 0 10px rgba(136, 136, 136, 0.125);
  outline: 0 none;
  border-color: #c1c9d0;
}

.form-control-sm {
  height: calc(1.5em + 1rem);
  padding: .4375rem .65625rem;
  font-size: .8125rem;
  line-height: 1.5;
  border-radius: .25rem;
}

.form-control-lg {
  height: calc(1.5em + 1.875rem);
  padding: .875rem 1.09375rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: .3125rem;
}

a {
  color: #377dff;
}

a:hover {
  color: #0052ea;
  text-decoration: none;
}

p {
  margin-top: 0;
  margin-bottom: 1rem;
}

p:last-child {
  margin-bottom: 0;
}

dl, ol, ul {
  margin-top: 0;
  margin-bottom: 0;
}

input,
button,
a {
  transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  -transition: all 0.4s ease;
  -webkit-transition: all 0.4s ease;
}

input,
input:focus,
button,
button:focus {
  outline: none;
}

input[type="file"] {
  
  min-height: calc(1.5em + .75rem + 2px);
}

